
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {

  props:{

  },
  name: 'health',
  components:{
    Group,
    XSwitch
  },
  data () {
    return {
      value1:1,
      illAllCheckStatus:false,
      illUl:[
        {
          text:'36-37 60-100/',
          isOk:false
        },
        {
          text:'19.3Kpa145mmhg12kpa96mmhg):I',
          isOk:false
        },
        {
          text:'',
          isOk:false
        }
      ]

    }
  },
  methods:{
    illAllCheck(value){
      this.illUl.map((valueObj)=>{
        valueObj.isOk=value;
      });
    }

  }
}



